  <script>
  import $ from 'jquery';
  import '../static';
  import Sidebar from '../components/Sidebar.vue';
  import Navbar from '../components/Navbar.vue';
  import Footer from '../components/Footer.vue';
  import { api, language } from '../../../helpers/api';
  import DataTable from 'datatables.net-vue3';
  import DataTablesCore from 'datatables.net';
  import Select from 'datatables.net-select';
  import { mapState } from 'vuex';
  
  DataTable.use(DataTablesCore);
  DataTable.use(Select);
  
  export default {
    name: "employer_view",
    components: { Sidebar, Navbar, Footer, DataTable },
    data() {
      return {
        title: "Funcionário",
        selectedRow: null,
        funcionarios: [],
        group: [],
        columns: [
          { data: 'nome' },
          { data: 'telefone' },
          { data: 'grupo' },
          {
            data: null,
            render: (data) => {
              return data.status ?
                `<span class="fas fa-check-circle text-success"></span> Ativo` :
                `<span class="fas fa-check-circle text-danger"></span> Inativo`;
            }
          },
        ],
        employer: {},
        filter_data: {},
        language:language
      }
    },
    computed:{
        ...mapState(['user'])
    },
    mounted() {
      this.getFuncionarios();
      this.getGroups();
    },
    methods: {
      getGroups() {
        this.group = [
          'Administrativo', 
          'Docente', 
          'Auxiliar'
        ]
      },
      getFuncionarios() {
        api.get(`/funcionarios/`)
          .then(res => {
            this.funcionarios = res.data;
          })
          .catch(err => {
            console.log(err);
          })
      },
      filterData(){
        api.get(`/funcionarios/grupo/${this.filter_data.grupo}`)
          .then(res => {
            this.funcionarios = []
            if(res.data.length > 0) {
                this.funcionarios = res.data;
            }else{
                this.$swal.fire({
                    title:"Erro",
                    text:"Nenhum funcionário encontrado neste grupo",
                    icon:"warning"
                })
            }
          })
          .catch(err => {
            console.log(err);
          })
      },
      submitForm() {
        if (this.employer.id > 0) {
          api.put(`/funcionarios/${this.employer.id}`, this.employer)
            .then(res => {
              if (res.data.message) {
                this.$swal.fire({
                  title: "Sucesso",
                  text: res.data.message,
                  icon: "success"
                });
                this.getFuncionarios();
                $(".btn-secondary").click();
              } else {
                this.$swal.fire({
                  title: "Erro",
                  text: "Não foi possível atualizar",
                  icon: "error"
                });
              }
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          api.post("/funcionarios/", {...this.employer, user_id:this.$store.state.user.id})
            .then(res => {
              if (res.data.data.id > 0) {
                this.$swal.fire({
                  title: "Sucesso",
                  text: res.data.message,
                  icon: "success"
                });
                this.getFuncionarios();
                $(".btn-secondary").click();
              } else {
                this.$swal.fire({
                  title: "Erro: Não foi possível cadastrar",
                  icon: "error"
                });
              }
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      deleteForm() {
        api.delete(`/funcionarios/${this.employer.id}`)
          .then(res => {
            if (res.data.message) {
              this.$swal.fire({
                title: "Sucesso",
                text: res.data.message,
                icon: "success"
              });
              this.getFuncionarios();
              $(".btn-secondary").click();
            } else {
              this.$swal.fire({
                title: "Erro: Não foi possível excluir",
                icon: "error"
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      },
      selectRow() {
        const table = this.$refs.table.dt;
        const self = this;  
        
        table.rows({ selected: true }).every(function () {
          const data = this.data();
          self.employer = data;
        });
      },
      clearForm() {
        this.employer = {};
      },
    },
  }
  </script>
  
